body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

ul, li, p {
  margin: 0;
  padding: 0;
}

h1 {
  margin: 20px auto;
}

button, .btn {
  width: 150px;
  padding: 5px;
  text-align: center;
  display: block;
  font-size: 20px;
  background-color: #09cf31;
  cursor: pointer;
  text-decoration: none;
  border: 1px solid #000;
}

.app {
  width: 900px;
  margin: 0 auto;
  text-align: center;
}

.currentWord-wrapper{
  margin-top: 100px;
  text-align: center;
}

.currentWord {
  display: inline-flex;
  justify-content: space-between;
}

.letter {
  list-style-type: none;
  width: 45px;
  font-size: 50px;
  color: #360695;
  font-weight: 500;
  text-align: center;
  vertical-align: middle;
  cursor: pointer;
}

.clickLetter-wrapper {
  margin-top: 20px;
  height: 30px;
}

.clickLetter {
  font-size: 18px;

}

.answer-wrapper {
  height: 50px;
}

.answer {
  font-size: 18px;
}

.wrongAnswer {
  padding: 0 10px;
  color: #ff0000;
  font-size: 24px;
}

.rightWord {
  padding: 0 10px;
  font-size: 24px;
}

.allright {
  color: #09cf31;
  font-weight: 500;
  font-size: 24px;
}

.button-wrapper {
  display: flex;
  justify-content: space-around;
  margin-top: 60px;
}

.hidden {
  display: none;
}

.red {
  color: red;
}

.nevsky {
  width: 600px;
  position: fixed;
  z-index: 10;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.nevsky img {
  width: 100%;
  max-height: 700px;
}

.close {
  position: absolute;
  height: 30px;
  top: 0;
  left: 100%;
  border: 1px solid #817f7f;
  font-size: 20px;
  padding: 0 10px;
  cursor: pointer;
}

.nevsky-wrapper {
  position: relative;
}

.countWords {
  margin-top: 20px;
}

.count-title {
  padding: 15px;
}

.count-number {
  font-weight: 500;
}

.starTitle {
  margin-top: 100px;
  font-size: 40px;
  font-weight: 600;
}

.startButton {
  margin-top: 100px;
  position: relative;
}

.left {
  position: absolute;
  top: 0;
  left: 100px;
}

.right {
  position: absolute;
}

button.right  {
  background-color: #ff0202;
}
